body {
  margin: 0;
  padding: 0;
}

#root {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

nav {
  background-color: lightblue;
  width: 100%;
}

nav > ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  margin: 0;
  padding: 0;
}

nav > ul > li > a {
  display: block;
  padding: 1em;
}

nav > ul > li > a,
nav > ul > li > a:visited {
  text-decoration: none;
  font-weight: bold;
}

.img-container {
  max-width: 80%;
}

.img-container > img {
  width: 100%;
  height: auto;
}
